import {
    mapMutations
} from 'vuex';

export default {
    data() {
        return {
            isShow: false,
            version: '',
            relation_id:'',
        };
    },
    onShow() {
        let that = this;
        let token = this.$queue.getData("token");
        let relation_id = this.$queue.getData("relation_id");
        if (relation_id) {
            this.relation_id = relation_id;
        }
        if (token) {
            this.isShow = true;
        }

    },
    methods: {
        ...mapMutations(['logout']),

        navTo(url) {
            let token = this.$queue.getData("token");
            if (token) {
                uni.navigateTo({
                    url: url
                });
            } else {
                uni.navigateTo({
                    url: '/pages/public/login'
                });
            }
        },
        navTo1(url) {
            uni.navigateTo({
                url: url
            });
        },
        about(url) {
            uni.showModal({
                showCancel: false,
                title: '省钱兄',
                content: '省钱兄是一个汇聚了全网隐藏优惠券的网购返利软件 ！这里汇聚淘宝、京东、天猫、拼多多等商城优惠券信息！每天实时更新优惠券，发放一些商家的大额优惠券！这里可以帮你网购省钱返利，也可以帮你兼职赚钱！每年最高能让你网购省50%的钱！\n\n西安省钱兄网络科技有限公司',
                success: (e) => {
                    if (e.confirm) {

                    }
                }
            });
        },
        //退出登录
        toLogout() {
            let that = this;
            uni.showModal({
                content: '确定要退出登录么',
                success: (e) => {
                    if (e.confirm) {
                        that.$queue.remove("token");
                        that.$queue.remove("userId");
                        that.$queue.remove("mobile");
                        that.$queue.remove("openid");
                        that.$queue.remove("nickName");
                        that.$queue.remove("image_url");
                        that.$queue.remove("relation_id");
                        that.isShow = false;
                        setTimeout(() => {
                            uni.switchTab({url: '/pages/member/user'});
                        }, 200)
                    }
                }
            });
        },
        //switch
        switchChange(e) {
            let statusTip = e.detail.value ? '打开' : '关闭';
            this.$api.msg(`${statusTip}消息推送`);
        },
        update() {



























        }

    }
}
